

import {defineComponent, onMounted, ref} from "vue";
import moment from "moment";
import Remote from "@/remote/scheduleRequest";
import Dialog from "@/components/dialog";
import {useStore} from "vuex";

declare var Affinity: any

export default defineComponent ({
    name: "CostDetailAnalysisV2",
    components: {

    },
    props: {
        fromDate: {
            type: Object
        },
        toDate: {
            type: Object
        },
        employeeList: {
            type: Array
        }
    },

    setup(props: any) {
        const store = useStore()
        //
        const data = ref(<any[]>[])
        const costRepeatNo = ref(0);
        const pageNumber = ref(1)
        const pageSize = ref(20)
        const totalPages = ref(5)
        const pageList = ref(<number[]> []);

        //
        // const fromDate = ref(<Moment | null>null);
        // const toDate = ref(<Moment | null>null);
        // const employeeList = ref(<number[]> []);

        const dataHeadings = ref(<any[]>[])
        const headingStyle = ref()
        const gridWidth = ref(2000)
        const girdWidthStyle = ref({width: '2000px'})

        const dataAnalysis = ref(<any[]>[]);
        const options = ref([
            "Start/End Time",
            "Detailed Cost Report"
        ]);
        const noDataError = ref(false)

        onMounted(() => {
            initialRequest()
        })

        async function initialRequest() {
            Dialog.loading()
            // await store.dispatch(EmployeeActions.getEmployees, Affinity.user.EmployeeNo).then((res: any) => {
            //     employeeList.value = res.map((employeeDetail: any) => employeeDetail.employeeNo)
            // })
            await loadGridData()

            Dialog.clear();
        }

        async function loadGridDataFromParent() {
            Dialog.loading()
            await loadGridData()
            Dialog.clear()
        }

        async function loadGridData() {
            noDataError.value = false;
            let fromDate = props.fromDate;
            let toDate = props.toDate;
            let employeeList = props.employeeList;
            if (!fromDate || !toDate) {
                fromDate = moment().startOf('month')
                toDate = moment().endOf('month')
            }

            const query = {
                // pageNumber: pageNumber.value,
                // pageSize: pageSize.value,
                sortBy: 'CommonName',
                sortingType: 'asc',
                pagingEnabled: true,
                pageNumber: pageNumber.value,
                pageSize: pageSize.value,
                employeeNumbers: employeeList,
                transDateFrom: fromDate ? fromDate.format('YYYY-MM-DD') : null,
                transDateTo: toDate ? toDate.format('YYYY-MM-DD') : null,
            }


            await Remote.GetCostAnalysisDetailV2(query).then(res => {
                if (res.response && res.response.length === 0) {
                    noDataError.value = true;
                    return;
                }
                res.response.forEach((data: any) => {
                    data.TransDate = moment(data.TransDate).format('YYYY-MM-DD')
                    data.TotalCost = Math.round(  data.TotalCost * 100) / 100

                    for (const [key, value] of Object.entries(data)) {
                        if (typeof value === 'number') {
                            data[key] = Math.round(  value * 100) / 100
                        }
                    }
                })

                let costKeyCount = -1
                const firstRow = res.response[0];
                for (const keys of Object.keys(firstRow)) {
                    if ((keys as string).indexOf("Cost") > -1) {
                        costKeyCount++
                    }
                }
                costRepeatNo.value = costKeyCount;
                dataHeadings.value = generateHeading(costRepeatNo.value)
                console.log('dataHeadings', costKeyCount)

                getHeadingStyle()
                getGridWidth()
                calculatePageList(res.currentPageNumber, res.totalPages)
                dataAnalysis.value = res.response;
            })
        }

        function generateHeading(repeatNo: number) {
            const staticHeading = [
                "Employee Number",
                "Employee Name",
                "Functional Area",
                "Date",
                "Pay Rate",
                "Total Hours",
                "Total Cost",
            ]

            const repeatHeadingOne = [
                "Description",
                "Hours",
                "Cost",
            ]

            let headings = staticHeading.slice()
            let i = 0;

            while(i < repeatNo) {
                const numberedRepeatHeading = repeatHeadingOne.map(heading => {
                    return heading + (i + 1);
                })
                headings.push(...numberedRepeatHeading);
                i++
            }

            return headings;
        }

        function getHeadingStyle() {
            let styleString = ''
            Array.from(Array(costRepeatNo.value)).map((_, i) => {
                styleString += ' 150px 100px 100px'
            });

            headingStyle.value =
            {
                'grid-template-columns': `150px 150px 150px 100px 100px 100px 100px${styleString}`
            }
        }

        function getGridWidth() {
            gridWidth.value = 850 + 350 * costRepeatNo.value + costRepeatNo.value * 3 + 6;
            console.log('gridWidth.value', gridWidth.value)
            girdWidthStyle.value = {
              'width': `${gridWidth.value}px`
            }
            return gridWidth.value;
        }

        function calculatePageList(pageNumber: number, pageSize: number) {
            if (pageSize <= 5) {
                pageList.value = Array.from(Array(pageSize).keys()).map(k => k + 1)
            } else {
                if (pageNumber <= 3) {
                    pageList.value = Array.from(Array(5).keys()).map(k => k + 1)
                } else {
                    if ( pageSize > pageNumber + 2) {
                        pageList.value = Array.from(Array(5).keys()).map(k => k + pageNumber - 2)
                    } else {
                        pageList.value = Array.from(Array(5).keys()).map(k => k + pageSize - 4)
                    }
                }
            }
        }

        async function selectPage(newPageNumber: number) {
            pageNumber.value = newPageNumber
            Dialog.loading()
            await loadGridData()
            Dialog.clear()
        }

        async function selectNextPage(pageDiff: number = 1) {
            pageNumber.value = pageNumber.value + pageDiff
            Dialog.loading()
            await loadGridData()
            Dialog.clear()
        }

        return {
            data, dataHeadings, dataAnalysis, options, headingStyle, girdWidthStyle,
            pageNumber, pageSize, totalPages, pageList, noDataError,
            selectPage, selectNextPage,
            // fromDate, toDate,
            loadGridData, loadGridDataFromParent}
    }
})
